  const preview = (billNo, viewmodel, params, beforeAct, afterAct) => {
    const actionName = params.params && params.params.cAction;
    if (actionName === 'localPrint') {
      const localPrintButtonModel = viewmodel.get('btnLocalPrint');
      if (localPrintButtonModel) {
        params.disabledCallback = function () {
          localPrintButtonModel.setDisabled(true);
        }
        params.enabledCallback = function () {
          localPrintButtonModel.setDisabled(false);
        }
      }
      viewmodel.biz.do(actionName, viewmodel, params);
      return;
    }
    beforeAct({ params }, function () {
      let printTplItemName = '';
      let template = '';
      let errInfo = '';

      if (params.cParameter) {
        const cParameter = JSON.parse(params.cParameter);
        printTplItemName = cParameter && cParameter.printTplItemName;
      }
      if (!printTplItemName) {
        errInfo = '请预置打印按钮的cParameter信息{printTplItemName : 打印模板的cItemName}';
      }
      else {
        template = viewmodel.get(printTplItemName).getValue();
        if (!template) {
          errInfo = '请选择打印模板。';
        }
      }
      if (errInfo != '') {
        cb.utils.alert(errInfo, 'warning');
        afterAct({ err: errInfo });
        return;
      }
      const inElectronApp = cb.electron.getSharedObject();
      const newTab = inElectronApp ? null : window.open('about:blank');
      const proxy = cb.rest.DynamicProxy.create({
        preview: { url: 'print/printPreview', method: 'POST' }
      })
      proxy.preview({
        route: params.cSvcUrl || '/bill',
        billno: billNo,
        template: template,
        ids: [viewmodel.get('id').getValue()]
      }, function (err, result) {
        if (!err) {
          const { user, serviceUrl } = cb.rest.AppContext;
          result = `${result}&lang=${user && user.locale || 'zh_CN'}`;
          if (inElectronApp) {
            cb.electron.sendOrder('openExternal', `${serviceUrl}${result}&token=${user && user.token || ''}`);
          } else {
            newTab.location.href = result;
          }
        }
        const afterActData = {
          err: err,
          res: result
        };
        afterAct(afterActData, common.afterAct);
      });
    });
  }

