  const initDefaultActions = (billNo, viewmodel, params, beforeAct, afterAct) => {
    const allActions = viewmodel.allActions
    const beforeActData = {
      params: params,
      data: allActions
    };
    beforeAct(beforeActData, function () {
      viewmodel.on('moveprev', function () {
        const prevAction = allActions.find((x) => {
          return x.cCommand && x.cCommand.trim().toLocaleLowerCase() === 'cmdmoveprev'
        })
        if (prevAction) {
          movePrevOrNext(billNo, viewmodel, prevAction)
        }
      })
      viewmodel.on('movenext', function () {
        const nextAction = allActions.find((x) => {
          return x.cCommand && x.cCommand.trim().toLocaleLowerCase() === 'cmdmovenext'
        })
        if (nextAction) {
          movePrevOrNext(billNo, viewmodel, nextAction)
        }
      })
      if (viewmodel.get(env.VOUCHER_TPL_KEY)) {
        viewmodel.get(env.VOUCHER_TPL_KEY).on('afterValueChange', function (data) {
          if (data && data.value) {
            viewmodel.biz.do('selecttpl', viewmodel, data)
          }
        })
      }
      afterAct()
    })
  }

  const selectTpl = function (billNo, oldVM, params, beforeAct, afterAct) {
    params.tplid = params.value.value
    const cbk = (err, result,) => {
      const vmAndView = common.initVM(err, result, billNo, params)
      if (vmAndView) {
        const { vm: newVM, view: newView } = vmAndView
        oldVM.communication({
          payload: {
            menuName: newView.cBillName,
            vm: newVM,
            refresh: true,
            metaData: newView
          }
        })
        _loadVm(oldVM, newVM)
        common.setMode(newVM, common.getMode(oldVM))
        let loadData,
          setData
        if (common.getMode(newVM) == env.VOUCHER_STATE_ADD) {
          loadData = oldVM.collectData(true)
          setData = oldVM.addData
        } else {
          loadData = oldVM.getOriginalData()
          setData = oldVM.collectData(true)
        }
        newVM.setData(setData)
        render(newVM, loadData, common.getMode(newVM) == env.VOUCHER_STATE_ADD)

        if (afterAct) afterAct()
      }
    }
    const beforeActData = {
      params: params,
    };
    beforeAct(beforeActData, function () {
      common.fetchMeta(billNo, params, cbk)
    })
  }

  const push = function (billNo, viewModel, params, beforeAct, afterAct) {
    console.log('begin-push--' + new Date().valueOf())
    if (viewModel) {
      common.push(viewModel, params, viewModel.collectData(true), beforeAct, afterAct);
      return;
    }
    if (!params || !params.cSvcUrl) return;
    const queryString = new cb.utils.queryString(params.cSvcUrl);
    const groupCode = queryString.get('groupCode'); const targetCode = queryString.get('code');
    if (!targetCode) return;
    const callback = function (err, pushData) {
      if (err) {
        if (window.parent !== window && cb.rest.AppContext.query.random && params.domain)
          window.parent.postMessage({ key: 'crossdomainpush', random: cb.rest.AppContext.query.random, args: err.message }, '*');
        else
          common.afterAct({ err });
        return;
      }
      if (window.parent !== window && cb.rest.AppContext.query.random && params.domain)
        window.parent.postMessage({ key: 'crossdomainpush', random: cb.rest.AppContext.query.random }, '*');
      if (!pushData.targetData) return;
      // fangqg: 默认新增，加上状态值会认为是假新增
      delete pushData.targetData._status;
      params.carryParams = params.carryParams || {};
      params.carryParams.externalData = pushData.targetData;
      params.targetData = pushData.targetData;
      /* 肖静说  一条数据直接进单据 */
      if (pushData.dimensionKeys && pushData.dimensionKeys.length > 1)
        params.dimensionKeys = pushData.dimensionKeys;
      params.dimensionGroupCode = groupCode;
      params.groupTaskKey = pushData.dimensionKeys && pushData.dimensionKeys.length && pushData.dimensionKeys[0].groupTaskKey;
      params.bWorkbench = !!groupCode;
      /* delete by jinzh1 非工作台模式  saveReturn不可为false */
      // if (params.bWorkbench == false)
      // if (params.bWorkbench)
      // params.saveReturn = false;
      pushData.targetData.dimensionKey = pushData.dimensionKeys && pushData.dimensionKeys[0] && pushData.dimensionKeys[0].key
      _getInitdata(billNo, params, function (err1, emptyData) {
        if (err1) {
          common.afterAct({ err1 });
          return;
        }
        console.log('_getInitdata-push--' + new Date().valueOf())
        afterAct(null, common.billMerge(emptyData, pushData.targetData));
        console.log('afterAct-push--' + new Date().valueOf())
      });
    };
    if (!cb.utils.isArray(params.data))
      params.data = [params.data];
    const ids = []; const detailIds = [];
    params.data.forEach(item => {
      ids.push(item.id);
      if (params.pushKey)
        detailIds.push(item[params.pushKey]);
    });
    const childIds = params.pushKey ? detailIds : Array.from(new Set(ids));
    const mainIds = params.pushKey ? Array.from(new Set(ids)) : undefined;
    const postData = {
      code: targetCode,
      isMainSelect: params.pushKey ? 0 : 1,
      childIds, // cb.utils.isArray(params.data) ? params.data : [params.data.id],
      mainIds,
      data: params.data,
      groupCode: groupCode
    };
    Object.assign(postData, params.carryParams);
    console.log('doProxy-push--' + new Date().valueOf())
    common.doProxy(params, callback, postData);
  }
  const compositeAction = (billNo, viewModel, params) => {
    let firstAction = null;
    let Act = null;
    try {
      const defineParams = JSON.parse(params.cParameter);
      firstAction = defineParams.action.split('&')[0].replace(/(\w)/, function (v) {
        return v.toUpperCase()
      });
      Act = viewModel.biz.action()[firstAction.trim().toLocaleLowerCase()];
      if (!Act) return;
      params.defineParams = defineParams;
    } catch (e) {
      console.error('compositeAction error: ' + e.message);
      return;
    }
    const beforeAct = (beforeActData, callback) => {
      viewModel.promiseExecute('before' + firstAction, beforeActData, callback);
    };
    const afterAct = (afterActData, callback) => {
      viewModel.promiseExecute('after' + firstAction, afterActData, function () {
        callback && callback(afterActData);
      });
    };
    Act(billNo, viewModel, params, beforeAct, afterAct, params.cShowCaption);
  }

