  const asyncMeta = function (billNo, params, billData, viewCallback) {
    return new Promise(function (resolve) {
      const { cardViewModel } = params;
      if (cardViewModel) {
        cardViewModel.biz.do('load', cardViewModel, {
          data: billData
        });
        cb.utils.loadingControl.end();
        return;
      }
      const vmParams = Object.assign({}, params)
      if (billData && billData.tplid && params && !params.tplid) {
        vmParams.tplid = billData.tplid
      }
      if (params && params.carryData) {
        billData = Object.assign(billData, params.carryData)
      }
      const cbk = function (err, meta) {
        vmParams.billData = billData
        _fetchMetaCallback(err, meta, billNo, viewCallback, vmParams, resolve, function () {
          common.fetchMeta(billNo, vmParams, cbk)
        })
      }
      common.fetchMeta(billNo, vmParams, cbk)
    });
  }

  const asyncWorkflow = function (businessKey) {
    return new Promise(function (resolve) {
      window.flowComp({
        width: 850,
        height: 510,
        appsource: env.appCode,
        businessKey,
        onload (callbackData) {
          resolve(callbackData.fieldAuth || 'workflownoctrl');
        }
      });
    });
  }

  const processWorkflow = function (vm, view, fieldAuths) {
    parseViewModel(vm, fieldAuths);
    parseContainer(view.view, fieldAuths);
  }

  const parseViewModel = function (vm, fieldAuths) {
    for (const attr in fieldAuths) {
      if (attr === 'mainTable') {
        parseMainEntity(vm, fieldAuths[attr]);
      } else {
        parseChildEntity(vm, attr, fieldAuths[attr]);
      }
    }
  }

  const parseMainEntity = function (vm, fieldAuthObj) {
    for (const attr in fieldAuthObj) {
      const model = vm.get(attr);
      if (!(model instanceof cb.models.BaseModel)) continue;
      const { visible, editable } = fieldAuthObj[attr];
      model._set_data('bShowIt', visible);
      model._set_data('bCanModify', editable);
    }
  }

  const parseChildEntity = function (vm, childrenField, fieldAuthObj) {
    const model = vm.get(childrenField);
    if (!(model instanceof cb.models.BaseModel)) return;
    const columns = model._get_data('columns');
    if (!columns) return;
    for (const attr in fieldAuthObj) {
      const column = columns[attr];
      if (!column) continue;
      const { visible, editable } = fieldAuthObj[attr];
      column.bShowIt = visible;
      column.bCanModify = editable;
    }
  }

  const parseContainer = function (container, fieldAuths) {
    if (container.containers) {
      container.containers.forEach(function (item) {
        parseContainer(item, fieldAuths);
      });
    } else if (container.controls) {
      parseControls(container, fieldAuths);
    }
  }

  const parseControls = function (container, fieldAuths) {
    const fieldAuthObj = fieldAuths[container.childrenField || 'mainTable'];
    if (!fieldAuthObj) return;
    const controls = [];
    container.controls.forEach(function (control) {
      const fieldAuth = fieldAuthObj[control.cItemName];
      if (fieldAuth && !fieldAuth.visible) return;
      controls.push(control);
    });
    container.controls = controls;
  }

  const _bindKeyboard = function (vm) {
    if (process.env.__KEYBOARD__ === false || process.env.__KEYBOARD__ === 'false') return;
    const allGroupCode = vm.getCache('containerCodes') || [];
    if (allGroupCode.length) {
      const results = getContainers(vm, allGroupCode);
      (results.length > 2) && vm.execute('afterInitKeyboard', results[0], results[1], results[2]);
    }
  }

  const getContainers = function (vm, containerCodes) {
    const idArr = [];
    const domArr = [];
    const firstEle = [];
    let firstElement = null;
    const xNum = containerCodes.length;
    for (let i = 0; i < xNum; i++) {
      const containerCode = containerCodes[i];
      const containerMeta = vm && vm.getViewMeta(containerCode);
      if (!containerMeta) return;
      const controlId = []; // 每个 group 下的 controlId
      const controlDom = []; // 每个 group 下的 controlDom
      getControls(vm, containerMeta, controlId, controlDom, firstEle);
      idArr[i] = controlId;
      domArr[i] = controlDom;
    }
    firstElement = firstEle.length && firstEle[0];
    return [idArr, domArr, firstElement];
  }

  const getControls = function (vm, container, resultId, resultDom, resultFirstEle) {
    const selectors = vm.getCache('selectors');
    if (!selectors) return;
    if (container.containers && !container.controls) {
      container.containers.forEach(item => {
        getControls(vm, item, resultId, resultDom, resultFirstEle);
      })
    } else if (container.controls) {
      container.controls.forEach(control => {
        if (control.cItemName) {
          const billNo = vm.getParams() && vm.getParams().billNo;
          const modelKey = control.cItemName;
          const id = `${billNo}|${modelKey}`;
          const controlDom = document.getElementById(id);
          const inputDom = controlDom && controlDom.querySelector(selectors.join(','));
          const controlModel = vm.get(modelKey);
          if (id) {
            resultDom.push(inputDom || null);
            resultId.push(id);
          }
          if (controlModel && !resultFirstEle.length) {
            const isVisible = (controlModel.getVisible() !== false);
            const notDisabled = (controlModel.getDisabled() !== true);
            const notReadOnly = (controlModel.getReadOnly() !== true);
            if (isVisible && notDisabled && notReadOnly)
              resultFirstEle.push(inputDom);
          }
        }
      })
    }
  }

  const afterInitData = async function (billNo, params, billData, viewCallback) {
    const promises = [];
    promises.push(asyncMeta(billNo, params, billData, viewCallback));
    if (billData && (billData.isWfControlled || billData.pk_procdefins) && billData.verifystate > 0)
      promises.push(asyncWorkflow(`${billNo}_${billData.id}`));
    const results = [];
    for (let i = 0, len = promises.length; i < len; i++)
      results.push(await promises[i]);
    const data = results[0];
    const { vm, view } = data;
    if (results[1]) {
      if (results[1] === 'workflownoctrl') {
        vm.getParams().workflowNoCtrl = true;
      } else {
        processWorkflow(vm, view, results[1]);
      }
      // fangqg: 走了工作流字段权限控制后，不再走单据编码规则设置
      if (vm.getParams().isCoded === true)
        vm.getParams().isCoded = 0;
    }
    const { masterOrgField } = vm.getParams();
    if (!cb.utils.isEmpty(masterOrgField) && vm.get(masterOrgField).get('bCheck')) {
      vm.on('afterCheck', () => {
        if (vm.getCache('initKeyboardComplete')) return;
        _bindKeyboard(vm);
        vm.setCache('initKeyboardComplete', true);
      });
    }
    vm.on('afterEdit', () => {
      _bindKeyboard(vm);
    });
    vm.promiseExecute('afterLoadMeta', data, function () {
      const title = view.cBillName;
      viewCallback(vm, view, title) // 先绑定view防止extend中 viewmode addProperty
      // vm.execute('afterMount');
      const gridModels = vm.getGridModels();
      // 添加孙表开关addGrandSonTable
      const addGrandSonTable = vm.get('addGrandSonTable');
      const billNo = vm.getParams().billNo;
      const userId = (cb.rest.AppContext.user || {}).id;
      gridModels.forEach(gridModel => {
        const childrenField = gridModel.getName();
        const cacheKey = `${billNo}_${userId}_${childrenField}`;
        let cacheWidth = null;
        cb.indexDB.openDB('cacheInfo', 1, ['billInfo'])
          .then(result => {
            if (result) {
              cb.indexDB.IDB_getData({ dbTableName: 'billInfo', dbName: 'cacheInfo', key: cacheKey }).then(result => {
                cacheWidth = result;
                if (cacheWidth)
                  for (const key in cacheWidth) {
                    if (key != 'indexedDB_id') gridModel.setColumnState(key, 'iColWidth', cacheWidth[key]);
                  }
              })
            } else {
              console.error('打开数据库失败，缓存功能不可用！')
            }
          })
          .catch(e => {
            console.error(e)
          })

        gridModel.on('afterSetDataSource', () => {
          const afterAct = gridModel.getCache('afterAct');
          if (afterAct)
            afterAct();
          setTimeout(function () {
            common.setGridActionStates(gridModel, vm, vm.getParams().mode);
            staterule.runGridActionStates(gridModel, vm, vm.getParams().mode);
          });
        });
        gridModel.on('afterMount', () => {
          common.setGridActionStates(gridModel, vm, vm.getParams().mode);
          staterule.runGridActionStates(gridModel, vm, vm.getParams().mode);
        });
        gridModel.on('afterInsertRow', () => {
          common.setGridActionStates(gridModel, vm, vm.getParams().mode);
          staterule.runGridActionStates(gridModel, vm, vm.getParams().mode);
        });
        gridModel.on('afterInsertRows', () => {
          common.setGridActionStates(gridModel, vm, vm.getParams().mode);
          staterule.runGridActionStates(gridModel, vm, vm.getParams().mode);
        });
        gridModel.on('afterInsertRowsFromRefer', args => {
          vm.biz.do('referrowscheck', vm, Object.assign({ childrenField: childrenField }, args));
        });
        if (addGrandSonTable && process.env.__THEMETYPE__ == 'ncc') {
          gridModel.on('beforeSetDataSource', function (dataList) {
            dataList.forEach(item => {
              if (item.id) {
                if (!item.rid) {
                  item.rid = item.id
                }
              } else {
                if (!item.rid) item.attachmentId = uuid();
              }
            });
            return dataList;
          });
          gridModel.on('beforeInsertRow', function (rowData) {
            rowData.row.rid = uuid();
            return rowData
          });
          gridModel.on('beforeInsertRows', function (data) {
            const list = data.rows;

            list.forEach(item => {
              if (item.id) {
                if (!item.rid) {
                  item.rid = item.id
                }
              } else {
                if (!item.rid) item.attachmentId = uuid();
              }
            });
            return list;
          });
        }

        // vm.on('afterEdit', () => {
        //   common.setGridActionStates(gridModel, vm, vm.getParams().mode);
        // });
        gridModel.on('colWidthResize', args => {
          const { columnKey, width } = args;
          if (!cacheWidth) cacheWidth = { indexedDB_id: cacheKey };
          cacheWidth[columnKey] = width;
          cb.indexDB.IDB_saveData(cacheWidth, 'billInfo', 'cacheInfo');
        });
        gridModel.on('colWidthReturn', args => {
          cacheWidth = { id: cacheKey, dbTableName: 'billInfo', dbName: 'cacheInfo'};
          cb.indexDB.IDB_deleteOneData(cacheWidth);
        })
      });

      // 关联附件管理
      const addAttachment = vm.get('addAttachment');
      if (addAttachment && process.env.__THEMETYPE__ == 'ncc') {
        gridModels.forEach(gridModel => {
          gridModel.on('beforeSetDataSource', function (dataList) {
            dataList.forEach(item => {
              if (item.id) {
                if (!item.attachmentId) {
                  item.attachmentId = item.id
                }
              } else {
                if (!item.attachmentId) item.attachmentId = uuid();
              }
            });
            return dataList;
          });
          gridModel.on('beforeInsertRow', function (rowData) {
            rowData.row.attachmentId = uuid();
            return rowData
          });
        })
      }
      // 管理分页按钮状态；
      _managePageInfoStatue(vm, billNo);
      const { billData, metaType } = vm.getParams();
      if (metaType === 'treevoucher') {
        const treeModel = vm.getTreeModel();
        _bindTreeAttachment(vm);
        if (treeModel && treeModel.getState('banQuery')) {
        } else {
          _loadTree(vm, {
            billnum: billNo,
            treename: common.getVmParams(vm, 'treeName')
          });
          _bindTreeSelect(vm, billNo);
        }
        // vm.biz.do('add', vm);
        // vm.setCache('lastMode', env.VOUCHER_STATE_ADD);
        if (vm.getParams().mode !== env.VOUCHER_STATE_ADD) {
          const treeModel = vm.getTreeModel();
          if (treeModel && !treeModel.getState('defaultSelectedKeys'))
            treeModel.setState('defaultSelectedKeys', true);
        } else {
          vm.biz.do('add', vm);
        }
      } else {
        vm.biz.do('load', vm, {
          data: billData
        })
      }
      if (vm.getCache('bindTreeSelect') && vm.getTreeModel()) {
        _bindTreeSelect(vm, billNo);
      }
      vm.biz.do('code', vm)
      vm.biz.do('org', vm)
      vm.biz.do('depends', vm)
      vm.biz.do('rule', vm)
      if (env.USEFORMULARULE) {
        vm.biz.do('formularule', vm)
      }
      if (env.USESTATERULE) {
        vm.biz.do('staterule', vm)
      }
      if (params.copy)
        vm.biz.do('copy', vm);
      _afterViewCallback(billNo, vm, params);
    })
  }
  const _handlePageInfoStatus = async function (viewmodel, params, result) {
    if (themeType != 'ncc') return false;
    const btnMovefirst = viewmodel.get('btnMovefirst');
    const btnMovelast = viewmodel.get('btnMovelast');
    const btnMoveprev = viewmodel.get('btnMoveprev');
    const btnMovenext = viewmodel.get('btnMovenext');
    const parentViewmodel = viewmodel.getCache('parentViewModel')
    const lastSearchCondition = parentViewmodel && parentViewmodel.getCache('lastSearchCondition');
    let curVoucherIndex = viewmodel.getCache('curVoucherIndex');
    let recordCount = viewmodel.getCache('recordCount');
    if (!lastSearchCondition || (cb.utils.isEmpty(curVoucherIndex) || !recordCount)) {
      btnMovefirst && btnMovefirst.setState('disabled', true);
      btnMovelast && btnMovelast.setState('disabled', true);
      btnMoveprev && btnMoveprev.setState('disabled', true);
      btnMovenext && btnMovenext.setState('disabled', true);
      return false;
    }
    let canLoad = null;// 是否可加载相邻单据
    if (params) {
      const {cAction} = params;
      switch (cAction) {
        case 'movenext':
          curVoucherIndex = curVoucherIndex + 1;
          break;
        case 'moveprev':
          curVoucherIndex = curVoucherIndex - 1;
          break;
        case 'movelast':
          curVoucherIndex = recordCount - 1;
          break;
        case 'movefirst':
          curVoucherIndex = 0;
          break;
        case 'delete': {
          recordCount = recordCount - 1;
          const deleteReturn = cb.extendConfig.voucher.deleteReturn;
          canLoad = deleteReturn;
          if (deleteReturn == 'movenext') {
            // 删除最后一条
            if (curVoucherIndex == recordCount) {
              canLoad = 'moveprev'
              curVoucherIndex = recordCount - 1;
            }
          } else if (deleteReturn == 'moveprev') {
            // 删除第一条
            if (curVoucherIndex == 0) {
              canLoad = 'movenext';
            }
            curVoucherIndex = curVoucherIndex - 1 < 0 ? 0 : curVoucherIndex - 1;
          }
          if (recordCount == 0) {
            canLoad = false;
            // 交互待定
          }
          viewmodel.setCache('curVoucherIndex', curVoucherIndex);
          viewmodel.setCache('recordCount', recordCount);
          return canLoad;
        }
        case 'save' : {
          const mode = common.getMode(viewmodel);
          if (mode == env.VOUCHER_STATE_EDIT) {
            return;
          }
          /* const billno=parentViewmodel.getParams().billNo;
             parentViewmodel.biz.action().refresh(billno,parentViewmodel) */
          const info = await _getParentAllList(parentViewmodel);
          const recordList = info.recordList || [];
          const index = recordList.findIndex(item => {
            return item.id == result.id;
          });
          if (index > -1) {
            recordCount = info.recordCount || recordList.length;
            curVoucherIndex = index;
          } else {
            recordCount = 1;
            curVoucherIndex = 0
          }
        }
      }
      viewmodel.setCache('curVoucherIndex', curVoucherIndex);
      viewmodel.setCache('recordCount', recordCount);
    }

    if (recordCount == 1) {
      btnMovefirst && btnMovefirst.setState('disabled', true);
      btnMovelast && btnMovelast.setState('disabled', true);
      btnMoveprev && btnMoveprev.setState('disabled', true);
      btnMovenext && btnMovenext.setState('disabled', true);
    } else {
      if (curVoucherIndex == -1) {
        // 新增
        btnMoveprev && btnMoveprev.setState('disabled', true);
        btnMovenext && btnMovenext.setState('disabled', true);
        btnMovefirst && btnMovefirst.setState('disabled', true);
        btnMovelast && btnMovelast.setState('disabled', true);
      } else if (curVoucherIndex == 0) {
        btnMovefirst && btnMovefirst.setState('disabled', true);
        btnMoveprev && btnMoveprev.setState('disabled', true);
        btnMovelast && btnMovelast.setState('disabled', false);
        btnMovenext && btnMovenext.setState('disabled', false);
      } else if (curVoucherIndex < recordCount - 1) {
        btnMovefirst && btnMovefirst.setState('disabled', false);
        btnMovelast && btnMovelast.setState('disabled', false);
        btnMoveprev && btnMoveprev.setState('disabled', false);
        btnMovenext && btnMovenext.setState('disabled', false);
      } else if (curVoucherIndex == recordCount - 1) {
        btnMovelast && btnMovelast.setState('disabled', true);
        btnMovenext && btnMovenext.setState('disabled', true);
        btnMovefirst && btnMovefirst.setState('disabled', false);
        btnMoveprev && btnMoveprev.setState('disabled', false);
      }
    }
  }
  const _getParentAllList = function (viewmodel) {
    return new Promise((resolve) => {
      const gridmodel = viewmodel.getGridModel();
      const proxyConfig = gridmodel._get_data('proxyConfig');
      if (!proxyConfig) return;
      const queryParams = gridmodel._get_data('queryParams');
      const proxy = cb.rest.DynamicProxy.create({ queryData: proxyConfig });
      delete queryParams.page;
      proxy.queryData(queryParams, function (err, result) {
        if (err) {
          cb.utils.alert(err.message, 'error');
          return;
        }
        resolve(result);
      });
    });
  }
  const _managePageInfoStatue = function (viewmodel, billNo) {
    if (themeType !== 'ncc') {
      return;
    }
    viewmodel.on('afterLoadData', function () {
      let curVoucherIndex = viewmodel.getCache('curVoucherIndex');
      let recordCount = viewmodel.getCache('recordCount');
      if (cb.utils.isEmpty(curVoucherIndex) || !recordCount) {
        const parentViewmodel = viewmodel.getCache('parentViewModel');
        if (parentViewmodel) {
          const parentCurVoucherIndex = parentViewmodel.getCache('curVoucherIndex');
          const parentPageInfo = parentViewmodel.getCache('pageInfo');

          if (!cb.utils.isEmpty(parentCurVoucherIndex) && parentPageInfo) {
            const {pageIndex, pageSize, recordCount: count} = parentPageInfo
            curVoucherIndex = (pageIndex - 1) * pageSize + parentCurVoucherIndex;
            recordCount = count;
            viewmodel.setCache('curVoucherIndex', curVoucherIndex);
            viewmodel.setCache('recordCount', recordCount);
          }
        }
      }
      _handlePageInfoStatus(viewmodel);
    })
  }
