import { runRule } from './rule';

export default function formulaRule (billNo, viewmodel, params, beforeAct, afterAct) {
  const rules = [];
  viewmodel.get('propertyNames').forEach(function (property) {
    if (viewmodel.get(property) instanceof cb.models.BaseModel) {
      // GridModel 和 SimpleModel
      if (viewmodel.get(property).modelType === 'GridModel') {
        viewmodel.get(property).get('fieldNames').forEach(function (fieldName) {
          const model = viewmodel.get(property).getEditRowModel().get(fieldName);
          if (model.get('iSystem') === 2) {
            rules.push({
              type: 0,
              // triggers: '',
              actionGroups: [{
                actions: [{
                  exprType: 0,
                  target: property + '.' + model.get('cItemName'),
                  scrExprType: 0,
                  srcExpression: model.get('formula')
                }]
              }]
            })
          }
        })
      } else {
        const model = viewmodel.get(property);
        if (model.get('iSystem') === 2) { // 非系统字段 配置了公式
          rules.push({
            type: 0,
            // triggers: '',
            actionGroups: [{
              actions: [{
                exprType: 0,
                target: model.get('cItemName'),
                scrExprType: 0,
                srcExpression: model.get('formula')
              }]
            }]
          })
        }
      }
    }
  })
  viewmodel.setCache('formulaRules', rules);
  runRule(viewmodel, rules, 'listener');
}
