export function mtlRequest (billNo, viewmodel, params, beforeAct, afterAct) {
  const data = _getParams(params);
  if (data.headers && typeof data.headers === 'string') {
    try {
      data.headers = JSON.parse(data.headers);
    } catch (e) {
      alert('headers 参数错误')
      return
    }
  }
  if (data.params && typeof data.params === 'string') {
    try {
      data.params = JSON.parse(data.params);
    } catch (e) {
      alert('params 参数错误')
      return
    }
  }
  mtl.request({
    // url: 'https://some-domain.com/api/user',
    // method: 'get',
    // headers: { 'X-Requested-With': 'XMLHttpRequest' },
    // params: {
    //   ID: 12345
    // },
    ...data,
    success: function (res) {
      const data = res.data; // 返回数据
      cb.invoker.publish('mtl', { action: 'request', result: data });
      _showPrompt(res);
    },
    fail: function (err) {
      const message = err.message; // 错误信息
      _showPrompt(message);
    }
  })
}

